              <path
                d="M455.2 9.2L256 208.4 56.8 9.2C44.5-3.1 24.6-3.1 12.2 9.2l-2.9 2.9C-3 24.4-3 44.4 9.3 56.7L208.4 256 9.2 455.2c-12.3 12.3-12.3 32.3 0 44.6l2.9 2.9c12.3 12.3 32.3 12.3 44.6 0L256 303.6l199.2 199.2c12.3 12.3 32.3 12.3 44.6 0l2.9-2.9c12.3-12.3 12.3-32.3 0-44.6L303.6 256 502.8 56.8c12.3-12.3 12.3-32.3 0-44.6l-2.9-2.9c-12.5-12.4-32.4-12.4-44.7-.1z"
              ></path>
            </svg>
          </div>
        </div>
      </div>
    </div>
  </transition>
  <div
    id="root"
    :class="`root ${
      isUserLoggedIn ? 'mn--max hd--expanded' : 'front-container'
    }`"
  >
    <section id="content" class="content">
      <loader v-if="globalAjaxRequest"></loader>
      <router-view />
    </section>
    <template v-if="appReady">
      <nav-bar></nav-bar>
      <app-header></app-header>
    </template>
  </div>
  <div class="d-none">
    <div id="order-preview">
      <OrderPreview
        :round="printableRound"
        :order="placedOrder"
        :items="roundItems"
      />
    </div>
  </div>
  <div class="d-none">
    <div id="invoice-preview">
      <InvoicePreview :order="printedInvoice" :items="invoiceItems" />
    </div>
  </div>

  <bootstrap-modal v-if="modalOpen" @close="toggleModal" modal-size="modal-sm">
    <template #head>
      <span>Select Branch For Printing</span>
    </template>
    <template #body>
      <div class="form-group mb-3">
        <select class="form-control-lg form-select" v-model="selectedBranch">
          <option value="null" hidden disabled>Select Branch</option>
          <option
            :value="branch.id"
            v-for="branch in branches"
            :key="branch.id"
          >
            {{ branch.name }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <div class="alert alert-warning">
          <span>
            <b> Note: </b> Before setting up a <b>printing branch</b>, please
            note that it can only be changed by an administrator.
          </span>
        </div>
        <button
          type="button"
          class="btn btn-primary w-100 btn-lg"
          @click="setPrintingBranch"
          :disabled="!selectedBranch"
        >
          Confirm
        </button>
      </div>
    </template>
  </bootstrap-modal>
  <div class="intercom-cta" @click="handlePrintRefesh" v-if="isFireFox">
    <svg
      height="24px"
      viewBox="0 0 16 16"
      xmlns="http://www.w3.org/2000/svg"
      fill="white"
    >
      <path
        d="M11 2H5a1 1 0 0 0-1 1v2H3V3a2 2 0 0 1 2-2h6a2 2 0 0 1 2 2v2h-1V3a1 1 0 0 0-1-1zm3 4H2a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h1v1H2a2 2 0 0 1-2-2V7a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v3a2 2 0 0 1-2 2h-1v-1h1a1 1 0 0 0 1-1V7a1 1 0 0 0-1-1z"
      ></path>
      <path
        fill-rule="evenodd"
        d="M11 9H5a1 1 0 0 0-1 1v3a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1v-3a1 1 0 0 0-1-1zM5 8a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-3a2 2 0 0 0-2-2H5z"
      ></path>
      <path d="M3 7.5a.5.5 0 1 1-1 0 .5.5 0 0 1 1 0z"></path>
    </svg>
  </div>
</template>
<script>
import AppHeader from "./shared/AppHeader.vue";
import NavBar from "./shared/NavBar.vue";
import Loader from "@/components/Loader.vue";
import OrderPreview from "@/index/shared/OrderPreview.vue";
import InvoicePreview from "@/index/shared/InvoicePreview.vue";
import BootstrapModal from "@/components/BootstrapModal.vue";
export default {
  components: {
    AppHeader,
    NavBar,
    Loader,
    OrderPreview,
    InvoicePreview,
    BootstrapModal,
  },
  data: () => ({
    printableRound: {},
    roundItems: [],
    printInterval: null,
    isFetchingRounds: false,
    placedOrder: {},
    latestPrintedRoundId: null,
    printedInvoice: {},
    invoiceItems: [],
    selectedBranch: null,
    printingBranch: null,
    branches: [],
    isFireFox: false,
  }),
  computed: {
    flashMessage() {
      return this.$store.state.flashMessage;
    },
    appSettings() {
      return this.$store.state.settings;
    },
    appReady() {
      return !this.$helper.empty(this.loggedUser);
    },
    globalAjaxRequest() {
      return (
        this.$store.state.isLoading &&
        !Object.keys(this.$store.getters.pendingRequests).length
      );
    },
    roundsUrl() {
      if (this.printingBranch) {
        return this.latestPrintedRoundId
          ? `next-printable-round?latest=${this.latestPrintedRoundId}&branch_id=${this.printingBranch}`
          : `next-printable-round?branch_id=${this.printingBranch}`;
      }
      return null;
    },
  },
  created() {
    if (this.isUserLoggedIn) {
      this.$http.get("auth/me").then((response) => {
        this.$store.commit("SET_CURRENT_USER", { ...response.data.user });
      });
      /*this.$http.get("branches").then((response) => {
        this.$store.commit("SET_BRANCHES", response.data.branches);
      }); */
    }
    this.$http
      .all([
        this.$http.post(
          "frontend/preloaders",
          this.$helper.generateFormData({
            keys: Object.keys(this.appSettings),
          })
        ),
        this.$http.get("pos-branches"),
      ])
      .then(
        this.$http.spread((responseOne, responseTwo) => {
          this.$store.commit("SET_APP_SETTINGS", responseOne.data);
          const { branches } = responseTwo.data;
          if (branches) {
            this.$store.commit("SET_BRANCHES", branches);
            this.branches = branches;
            if (
              navigator.userAgent.indexOf("Firefox") > 0 ||
              navigator.userAgent.toLowerCase().indexOf("firefox") !== -1
            ) {
              const printingBranch =
                window.localStorage.getItem("__printing_branch");
              if (!printingBranch) {
                this.toggleModal();
              }
            }
          }
        })
      );
    /*this.$http
      .post(
        "frontend/preloaders",
        this.$helper.generateFormData({
          keys: Object.keys(this.appSettings),
        })
      )
      .then((response) => {
        this.$store.commit("SET_APP_SETTINGS", response.data);
      }); */
  },
  methods: {
    handlePrintRefesh() {
      localStorage.removeItem("__printing_tab");
      sessionStorage.removeItem("isPrinting");
      setTimeout(() => {
        location.reload();
      }, 1000);
    },
    setPrintingBranch() {
      window.localStorage.setItem("__printing_branch", this.selectedBranch);
      this.printingBranch = this.selectedBranch;
      this.$store.commit("SET_FLASH_MESSAGE", {
        type: "success",
        message: "Branch Set successfully",
      });
      this.toggleModal();
    },
  },
  mounted() {
    this.$nextTick(() => {
      const lastPrintedRound = window.localStorage.getItem(
        "__last_printed_round"
      );
      if (lastPrintedRound) {
        this.latestPrintedRoundId = lastPrintedRound;
      }
      if (
        navigator.userAgent.indexOf("Firefox") > 0 ||
        navigator.userAgent.toLowerCase().indexOf("firefox") !== -1
      ) {
        this.isFireFox = true;
        setTimeout(() => {
          const printingTab = localStorage.getItem("__printing_tab");
          if (!printingTab) {
            localStorage.setItem(
              "__printing_tab",
              Math.random().toString(36).slice(2)
            );
            sessionStorage.setItem("isPrinting", true);
            const printingBranch =
              window.localStorage.getItem("__printing_branch");
            if (printingBranch) {
              this.printingBranch = printingBranch;
            }
            this.printInterval = setInterval(() => {
              if (!this.isFetchingRounds && this.printingBranch) {
                this.isFetchingRounds = true;
                this.$store.commit("SET_REQUEST_FLAG", "GETTING_LOCKED_TABLES");
                this.$http.get(this.roundsUrl).then((response) => {
                  this.isFetchingRounds = false;
                  if (response.data.status) {
                    const round = response.data.round;
                    const items = response.data.items;
                    const order = response.data.order;
                    if (round.category == "ORDER") {
                      this.printableRound = round;
                      this.roundItems = items;
                      this.placedOrder = order;
                    } else {
                      if (
                        Array.isArray(round?.items) &&
                        round?.items?.length > 0
                      ) {
                        order.grand_total = round?.items.reduce(
                          (a, b) => a + Number(b.amount),
                          0
                        );
                      }
                      this.printedInvoice = order;
                      this.invoiceItems = items;
                    }
                    this.latestPrintedRoundId = round.id;
                    window.localStorage.setItem(
                      "__last_printed_round",
                      this.latestPrintedRoundId
                    );
                  }
                });
              }
            }, 4000);
          }
        }, 2000);
      }
    });
  },
  watch: {
    $route(to) {
      if (["Orders"].includes(to.name)) {
        document.body.classList.add("overflow-hidden");
      } else {
        document.body.classList.remove("overflow-hidden");
      }
    },
  },
};
</script>
